import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getDataBaki, getDataKondisiBarang } from "../../../actions/datamaster_action";
import { ReanderField } from "../../../components/helpers/field";
const maptostate = (state) => {
  // console.log(state.datamaster.setFocus);
  // state.datamaster.setFocus ===true ? document.getElementById("kode_barcode").focus() : null
  return {
    databaki: state.datamaster.getDataBaki,
    datakondisi: state.datamaster.getDataKondisiBarang,
    setHancurBarang: state.databarang.setHancurBarang,
    focus: state.datamaster.setFocus,
  };
};
class FIlterPencarianBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      kondisi_barang: "kosong",
    };
  }
  componentDidMount() {
    this.props.dispatch(getDataKondisiBarang());
    this.props.dispatch(getDataBaki());
    setTimeout(() => {
      // console.log(this.props.focus);
      // if (this.props.focus === true) {
      //   document.getElementById("kode_barcode").focus()
      // } else {
        
      // }
      document.getElementById("kode_barcode").focus()
    }, 100);
  }
  
  render() {
    return (
      <div className="row">
          <div className="col-lg-3">
            <Field
              id="kode_barcode"
              onChange={(e) => this.props.getKodeBarcode(e.target.value)}
              name="kode_barcode"
              component={ReanderField}
              type="text"
              label="Kode Barcode"
              placeholder="Masukan Kode Barcode"
            />
          </div>
          <div className="col-lg-3">
            <Field
              onChange={(e) => this.props.getNamaBarang(e.target.value)}
              name="nama_barang"
              component={ReanderField}
              type="text"
              label="Nama Barang"
              placeholder="Masukan Nama Barang"
            />
        </div>
          <div className="col-lg-3">
            <Field
              onChange={(e) => this.props.getByKodeGroup(e.target.value)}
              name="kode_group"
              component={ReanderField}
              type="text"
              label="Kode Group"
              placeholder="Masukan Kode Group"
            />
        </div>
        <div className="col-lg-3">
          <Field
            label="Kode Baki"
            name="kode_baki"
            onChange={(e)=>this.props.getBarangKodeBaki(e.target.value)}
            // options={this.props.databaki
            //   .map((databaki) => {
            //     let data = {
            //       value: databaki.kode_baki,
            //       name: databaki.kode_baki + " - " + databaki.nama_baki,
            //     };
            //     return data || [];
            //   })}
            // onChange={(e)=>this.props.getBarangKodeBaki(e)}
            placeholder={"Silahkan Masukan Kode Baki"}
            component={ReanderField}
          />
        </div>
        </div>
    );
  }
}

FIlterPencarianBarang = reduxForm({
  form: "FIlterPencarianBarang",
  enableReinitialize: true,
})(FIlterPencarianBarang);
export default connect(maptostate, null)(FIlterPencarianBarang);
